<template>
    <div class="p-20">
        <div style="margin: 20px">
            <span>资产名称：</span>
            <el-input v-model="queryData.chineseName" placeholder="请输入内容" style="width: 300px"></el-input>

            <el-button type="primary" @click.stop="initData" style="margin-left: 10px">查询</el-button>
        </div>

        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="id" label="资产ID"></el-table-column>
            <el-table-column prop="coinName" label="资产名称"></el-table-column>
            <el-table-column prop="coinAddrNum" label="可用地址数量"></el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="queryData.page.pageNum"
                           :page-sizes="[10,15,20,30, 50]" :page-size="queryData.page.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

    </div>
</template>

<script>

    import fetch from '@/utils/fetch';

    export default {
        data() {
            return {
                tableData: [],
                queryData: {
                    chineseName: '',
                    page: {
                        pageNum: 1,
                        pageSize: 15
                    },
                },
                total: 0
            }
        },
        methods: {
            initData() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/getAvailableAddress',
                        method: 'post',
                        data: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            //翻页的按钮
            userPage(val) {
                this.queryData.page.pageNum = val;
                this.initData();
            },
            //获取每页多少条
            userPageSize(val) {
                this.queryData.page.pageSize = val;
                this.initData();
            },
        },
        created() {
            this.initData();
        },
    }
</script>


